@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin left($l) {
  left: $l;
}
@mixin right($r) {
  right: $r;
}

.wishlist-target {
	.arn_icon-add-wishlist {
		&:hover {
			fil: red;
		}
	}
}

.section-featured-collections {
	.tns-nav {
		position: relative;
		margin-top: 10px;
	}
	.slider-controls {
		button {
			top: 26%;
		}
		.slider-prev-button {
			@include left(-10px);
		}
		.slider-next-button {
			@include right(-10px);
		}
	}
	.block-heading-wrapper {
		margin-bottom: 15px;
		.block-heading {
			@include margin-end(20px);
			font-size: 20px;
			line-height: 1.2em;
		}
	}
	&.has-border {		
		.featured-collections-list {
			>li:not(:first-child) {
				margin-top: 30px;
				padding-top: 30px;
				border-top: 1px solid var(--miscellaneous-color);
			}
		}
	}
	.featured-collections-item {
		position: relative;
		>p {
			margin: 12px 0;
		}
	}
	.tns-horizontal.tns-subpixel>.tns-item {
		display: flex;
	}
	.collection-count {
		line-height: 1.45em;
	}
	.section-container {
		position: relative;
	}
	.offer-banner {
		position: relative;
		height: calc(100% - 30px);
		figure {
			height: 100%;
		}
	}
	.special-offer {
		flex: 0 0 370px;
		max-width: 100%;
		padding-bottom: 20px;
	}
	.offer-content {
		padding: 20px;
		width: 100%;
		p {
			font-size: 25px;
			line-height: 1.2em;
			margin: 0
		}
		.text-1 {
			margin-bottom: 20px;
		}
		.text-2 {
			margin-bottom: 20px;
			p {
				font-size: 36px;
				line-height: 1.12em;
			}
		}
		.product-title {
			font-size: 35px;
			line-height: 1.2em;
		}
		.product-weight {
			margin: 0 0 20px;
			font-size: 28px;
		}
		.product-price {
			margin: 20px 0;
			font-size: 36px;
			line-height: 1.12em;
			font-weight: 600;
			color: inherit;
		}
		price-item {
			color: inherit;
		}
		.price-compare {
			font-weight: 400;
			font-size: 26px;
		}
		button {
			display: flex;
			align-items: center;
			min-height: 46px;
			span {
				display: flex;
			}
			svg {
				@include margin-end(13px);
			}
		}
	}
	.collection-view-all {
		margin-bottom: 30px;
		margin-top: 10px;
		text-align: center;
		a {
			color: var(--primary-color);
			font-size: 15px;
			text-decoration: underline;
			text-underline-offset: 1px;
		}
	}
	.promotion-applies {
		.flex {
			justify-content: center;
			border: 1px dashed currentColor;
			max-width: 720px;
			width: 100%;
			margin: 0 auto;
			font-size: 13px;
			line-height: 1.3em;
			padding: 5px 15px;
			font-weight: 500;
			border-radius: 5px;
		}
		span.icon {
			display: flex;
			@include margin-end(13px);
		}
		span.text {
			display: flex;
			align-items: center;
			line-height: 1.2em;
		}
	}
	slider-component + .promotion-applies {
		margin-top: 35px;
	}
	@media (min-width: 750px) {
		.tns-nav {
			margin-top: 0;
		}
		.promotion-applies {
			.flex {
				font-size: 14px;
			}
		}
		&.has-border {		
			.featured-collections-list {
				>li:not(:last-child) {
					.tns-nav {
						margin-bottom: 60px;
					}
				}
			}
			.featured-collections-list {
				>li:not(:first-child) {
					margin-top: 60px;
					padding-top: 60px;
				}
			}
		}
		.slider-controls {
			button {
				top: 27%;
			}
		}
	}
	@media (min-width: 1000px) {
		.special-offer {
			@include margin-end(20px);
			padding-bottom: 0;
		}
		.flex-reverse {
			.special-offer {
				@include margin-start(20px);
				@include margin-end(0);
			}
		}
		.collection-view-all {
			margin-bottom: 50px;
		}
		.block-heading-wrapper {
			.block-heading {
				font-size: 35px;
			}
		}
	}
	@media (min-width: 1300px) {
		.special-offer {
			@include margin-end(50px);
		}
		.flex-reverse {
			.special-offer {
				@include margin-start(50px);
				@include margin-end(0);
			}
		}
		.offer-content {
			padding: 40px;
		}
		.slider-controls {
			.slider-prev-button {
				@include left(-25px);
			}
			.slider-next-button {
				@include right(-25px);
			}
		}
	}
}

.templateList-collections {
	.section-featured-collections {
		.featured-collections-item {
			margin-bottom: 30px;
			padding-bottom: 30px;
			border-bottom: 1px solid var(--miscellaneous-color);
		}
	}
	@media (min-width: 750px) {
		.section-featured-collections {
			.featured-collections-item {
				margin-bottom: 70px;
				padding-bottom: 70px;
			}
		}
	}
}